.navbar-header a,
ul.dropdown-menu a,
ul.navbar-nav a {
    border-bottom-style: none;
    color: #323232;
    font-weight: 700;
}

.swh-browse-nav li a {
    border-radius: 4px;
}

.nav-link.active {
    background-color: #e7e7e7;
}

.scrollable-menu {
    max-height: 180px;
    overflow-x: hidden;
}

.swh-corner-ribbon {
    width: 200px;
    background: #e43;
    position: absolute;
    text-align: center;
    line-height: 50px;
    letter-spacing: 1px;
    color: #f0f0f0;
    box-shadow: 0 0 3px rgba(0, 0, 0, 0.3);
    top: 25px;
    right: -50px;
    left: auto;
    -webkit-transform: rotate(45deg);
            transform: rotate(45deg);
    z-index: 2000;
}

.swh-loading {
    display: none;
}

.swh-loading.show {
    display: inline-block;
    position: fixed;
    background: white;
    border: 1px solid black;
    top: 50%;
    left: 50%;
    margin: -50px 0 0 -50px;
    text-align: center;
    z-index: 100;
}

.swh-metadata-table-row {
    border-top: 1px solid #ddd !important;
}

.swh-metadata-table-key {
    min-width: 200px;
    max-width: 200px;
    width: 200px;
}

.swh-metadata-table-value pre {
    white-space: pre-wrap;
}

.swh-table-even-odd th {
    border-top: none;
}

.swh-table-even-odd tr:nth-child(even) {
    background-color: #f5f5f5;
}

.swh-table-even-odd tr:nth-child(odd) {
    background-color: #fff;
}

.swh-table-cell-text-overflow {
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
}

.swh-directory-table {
    margin-bottom: 0;
}

.swh-directory-table td {
    border-top: 1px solid #ddd !important;
}

.swh-title-color {
    color: #e20026;
}

.swh-log-entry-message {
    min-width: 460px;
    max-width: 460px;
    width: 460px;
}

